<template>
  <div>
    <ion-page>
      <ion-header>
        <ion-toolbar>
          <ion-title style="font-size: 25px; font-weight: 600"
            >Reject Test</ion-title
          >
          <ion-buttons slot="end">
            <ion-button :strong="true" @click="handleDialog">
              <ion-icon size="large" :icon="close"></ion-icon>
            </ion-button>
          </ion-buttons>
        </ion-toolbar>
      </ion-header>
      <ion-content>
        <ion-list>
          <ion-item>
            <ion-label style="font-size: 20px; font-weight: 600"
              >Test Type</ion-label
            >
            <ion-label slot="end" style="font-size: 18px;">{{ details?.test_type_name }}</ion-label>
          </ion-item>
          <ion-item>
            <ion-label style="font-size: 20px; font-weight: 600"
              >Specimen</ion-label
            >
            <ion-label slot="end" style="font-size: 18px;">{{ details?.specimen_type }}</ion-label>
          </ion-item>
          <ion-item>
            <ion-label style="font-size: 20px; font-weight: 600"
              >Patient Name</ion-label
            >
            <ion-label slot="end" style="font-size: 18px;">
                {{ details?.client ?
                    `${details.client.first_name} ${details.client.middle_name ? details.client.middle_name : ''} ${details.client.last_name}` :
                    ''
                }}
              </ion-label>

          </ion-item>
          <ion-item>
            <ion-label style="font-size: 20px; font-weight: 600"
              >Accession Number</ion-label
            >
            <ion-label slot="end" style="font-size: 18px;">{{ details?.accession_number }}</ion-label>
          </ion-item>
          <ion-item>
            <ion-label style="font-size: 20px; font-weight: 600"
              >Person Talked To:
            </ion-label>
            <ion-input
              v-model="personTalkedTo"
              required
              placeholder="Enter full name"
              @ion-focus="enterInput('person')"
              style="font-size: 18px;"
            />
          </ion-item>

          <ion-item>
            <ion-label style="font-size: 20px; font-weight: 600"
              >Rejection Reason:
            </ion-label>
            <ion-select required v-model="selectedStatusReason">
              <ion-select-option
                v-for="reason in statusReasons"
                v-bind:key="reason.description"
                :value="reason.description"
                style="font-size: 18px;"
                >{{ reason.description }}</ion-select-option
              ></ion-select
            >
          </ion-item>
        </ion-list>
        <div class="keyboard" v-show="showKeyboard">
          <SimpleKeyboard
            @onChange="onChange"
            @onKeyPress="onKeyPress"
            :input="personTalkedTo"
            :inputName="personTalkedTo"
          />
        </div>
      </ion-content>

      <ion-footer>
        <ion-toolbar color="light" style="padding: 5px">
          <ion-button
            slot="start"
            size="large"
            color="danger"
            @click="cancelReject"
          >
            Cancel
          </ion-button>
          <ion-button
            slot="end"
            :disabled="!isset"
            size="large"
            color="success"
            @click="submitForm"
          >
            Reject
          </ion-button>
        </ion-toolbar></ion-footer
      >
    </ion-page>
  </div>
</template>

<script setup lang="ts">
import { Client } from "@/composables/client";
import { computed, onMounted, ref } from "vue";
import { useRoute, useRouter } from "vue-router";
import { close } from "ionicons/icons";
import { alertConfirmation, toastSuccess } from "@/utils/Alerts";
import {
  IonButtons,
  IonButton,
  IonIcon,
  IonHeader,
  IonContent,
  IonToolbar,
  IonTitle,
  IonItem,
  IonLabel,
  IonFooter,
  IonList,
  IonPage,
  IonSelect,
  IonSelectOption,
  IonInput,
} from "@ionic/vue";
import SimpleKeyboard from "@/components/Keyboard/SimpleKeyBoard.vue";

const route = useRoute();
const router = useRouter();
const client = Client();
const details = ref<any>({});
const personTalkedTo = ref<string>("");
const statusReasons = ref<Array<{ id: number; description: string }>>([]);
const selectedStatusReason = ref("");

const getTest = async () => {
  const res = await client.getJson(`/tests/${route.params.id}`);
  details.value = res?.data;
};
const handleDialog = () => {
  router.back();
};
const cancelReject = async () => {
  const ok = await alertConfirmation("Are you sure you want to cancel?");
  if (ok) handleDialog();
};
const emits = defineEmits(["updated"]);

const showKeyboard = ref<boolean>(false);

const enterInput = (value: string) => {
  if (value) showKeyboard.value = true;
};

const onChange = (input: string) => {
  personTalkedTo.value = input;
};

const onKeyPress = (input: string) => {
  if (input == "{enter}") {
    showKeyboard.value = false;
  }
};

const getStatusReasons = async () => {
  const res = await client.getJson("status_reasons");
  statusReasons.value = res?.data;
};
const isset = computed(
  () => personTalkedTo.value && selectedStatusReason.value
);

const submitForm = async () => {
  const reasonId = statusReasons.value.filter(
    (status) => status.description == selectedStatusReason.value
  );
  const res = await client.putJson(
    `test_statuses/${route.params.id}/rejected?status_reason_id=${reasonId[0].id}&person_talked_to=${personTalkedTo.value}`,
    {}
  );
  if (res?.data) {
    toastSuccess("Test Rejected Successfully");
    router.push("/tests");
    emits("updated", true);
  }
};
onMounted(() => {
  getTest();
  getStatusReasons();
});
</script>

<style scoped>
.keyboard {
  width: 100%;
  bottom: 0;
  position: absolute !important;
  font-size: 30px;
  text-transform: uppercase;
  font-weight: 400;
}
</style>
